import React from 'react'
import { Link } from 'react-router-dom'

function Nav() {
        return (
            <nav>
                    <p id="logo">EMILIA UTHURRALT</p>
                    <ul id='botonesnav'>
                        <li><Link to="/">Inicio</Link></li>
                        <li><Link to="/cyberland">Cyberland</Link></li> 
                    </ul>
            </nav>
        )
}

export default Nav